.date {
  display: flex;
  align-items: center;
  justify-content: start;
  color: #999;

  &--project {
    order: -1;
    margin: 0;
    margin-bottom: 2px;
    color: $light-color;

    @include mobile {
      margin-bottom: 6px;
    }
  }

  &__icon {
    margin-right: 4px;
    width: 14px;
    height: 14px;
    fill: currentColor
  }

  &__text {
    font-weight: 400;
    font-size: 12px;
    line-height: 22px;
  }
}
