.about {
  padding: 30px 0 70px;
  background-color: $dark-color;
  @include small-desktop {
    padding: 30px 0 81px;
  }

  @include medium-tablet {
    padding: 30px 0 64px;
  }
  @include small-mobile {
    padding: 34px 0 58px;
  }

  &__title {
    margin-bottom: 5px;
    color: $light-color;

    @include medium-tablet {
      margin-bottom: 6px;
    }
    @include small-mobile {
      margin-bottom: 3px;
    }
  }

  &__content {
    align-items: start;
    @include small-mobile {
      row-gap: 23px;
    }
  }

  &__descr {
    grid-column: 6 span;
    margin: 0;
    font-weight: 400;
    font-size: $main-size;
    line-height: 28px;
    color: $silver-color;

    @include small-desktop {
      grid-column: 4 span;
    }
    @include medium-tablet {
      grid-column: 3 span;
    }
    @include mini-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 4 span;
    }
    @include small-mobile {
      grid-column: 2 span;
      font-size: 14px;
      line-height: 25px;
    }
  }

  &__right {
    grid-column: 6 span;

    @include small-desktop {
      grid-column: 4 span;
    }
    @include medium-tablet {
      grid-column: 3 span;
    }
    @include mini-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 4 span;
    }
    @include small-mobile {
      grid-column: 2 span;
    }
  }

  &__img {
    margin-bottom: 25px;

    @include small-desktop {
      margin-bottom: 20px;
      min-height: 332px;
    }
    @include medium-tablet {
      min-height: 568px;
    }
    @include small-mobile {
      min-height: 278px;
    }
  }
}

.about-form {
  &__bottom {
    @include flex-v-center;

    @include small-desktop {
     flex-direction: column;
     align-items: start;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 13px;
    font-family: $second-family;
    font-weight: 400;
    font-size: $title-size;
    line-height: 22px;
    color: $light-color;

    @include small-desktop {
      margin-bottom: 15px;
    }
    @include small-mobile {
      font-size: 18px;
    }
  }

  &__input {
    margin-right: 30px;
    padding: 15px 23px;
    border: 1px solid #4a4a4a;
    outline: none;
    width: 100%;
    font-weight: 400;
    font-size: $main-size;
    color: $silver-color;
    background-color: #4a4a4a;

    @include small-desktop {
      margin-bottom: 20px;
      max-width: 342px;
    }
    @include mobile {
      padding: 12px 23px;
      width: 100%;
      max-width: none;
      font-size: 14px;
    }

    &:focus {
      outline: 1px solid $accent-color;
    }

    @include hover {
      color: $accent-color;
    }

    &:active {
      outline-color: #4a4a4a;
      color: $light-color;
    }
  }
}

