.just-validate-error-label {
  position: absolute;
  top: 3px;
  left: 0;
  padding-left: 22px;
  // padding-bottom: 2px;
  width: 100%;
  font-family: $font-family;
  font-weight: 400;
  font-size: 9px;
  text-align: start;
  color: #f06666;
}
.about-form__input.just-validate-error-field {
  outline-color: #f06666;
}
