.promo{
  padding: 41px 0 66px;
  background-color: $bg-light-color;

  @include small-desktop {
    padding: 34px 0 68px;
  }
  @include medium-tablet {
    padding: 79px 0 68px;
  }
  @include small-mobile {
    padding: 75px 0 2px;
  }

  &__event,
  &__works {
    grid-column: 6 span;
    height: 100%;

    @include small-desktop {
      grid-column: 4 span;
    }
    @include medium-tablet {
      grid-column: 3 span;
    }
    @include mini-tablet {
      grid-column: 6 span;
      min-height: 422px;
    }
    @include mobile {
      grid-column: 4 span;
    }
    @include small-mobile {
      grid-column: 2 span;
    }
  }

  &__works {
    @include small-mobile {
      margin-top: 5px;
    }
  }
}

.event {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: end;
  padding: 60px;
  color: var(--light-color);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url(../img/promo/events_1.jpg);
  background-image: image-set(
    "../img/promo/events_1@2x.webp" type("image/webp") 2x,
    "../img/promo/events_1.webp" type("image/webp") 1x,
    "../img/promo/events_1@2x.jpg" type("image/jpg") 2x,
    "../img/promo/events_1.jpg" type("image/jpg") 1x
  );

  @include small-desktop {
    padding: 30px;
    background-image: url(../img/promo/events_1-1024.jpg);
    background-image: image-set(
      "../img/promo/events_1-1024@2x.webp" type("image/webp") 2x,
      "../img/promo/events_1-1024.webp" type("image/webp") 1x,
      "../img/promo/events_1-1024@2x.jpg" type("image/jpg") 2x,
      "../img/promo/events_1-1024.jpg" type("image/jpg") 1x
    );
  }
  @include medium-tablet {
    background-image: url(../img/promo/events_1-768.jpg);
    background-image: image-set(
      "../img/promo/events_1-768@2x.webp" type("image/webp") 2x,
      "../img/promo/events_1-768.webp" type("image/webp") 1x,
      "../img/promo/events_1-768@2x.jpg" type("image/jpg") 2x,
      "../img/promo/events_1-768.jpg" type("image/jpg") 1x
    );
  }
  @include mobile {
    background-image: url(../img/promo/events_1-376.jpg);
    background-image: image-set(
      "../img/promo/events_1-376@2x.webp" type("image/webp") 2x,
      "../img/promo/events_1-376.webp" type("image/webp") 1x,
      "../img/promo/events_1-376@2x.jpg" type("image/jpg") 2x,
      "../img/promo/events_1-376.jpg" type("image/jpg") 1x
    );
  }

  &__category {
    position: absolute;
    top: 15px;
    right: 15px;

    @include small-mobile {
      top: 5px;
      right: 5px;
    }
  }

  &__author {
    display: inline-block;
    margin-bottom: 3px;
    font-family: $font-family;
    font-weight: 400;
    font-size: $main-size;
    color: $light-color;

    @include medium-tablet {
      margin-bottom: 14px;
    }
    @include small-mobile {
      margin-bottom: 7px;
      font-size: $mobile-text-size;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 19px;
    font-family: $second-family;
    font-weight: 700;
    font-size: 36px;
    line-height: 104%;
    color: $light-color;

    @include small-desktop {
      font-size: 34px;
      line-height: 100%;
    }
    @include medium-tablet {
      margin-bottom: 21px;
      line-height: 90%;
    }
    @include mini-tablet {
      max-width: 420px;
    }
    @include small-mobile {
      margin-bottom: 14px;
      max-width: 370px;
      font-size: 24px;
      line-height: 110%;
    }
  }
}

.works {
  &__title {
    margin: 0;
    margin-bottom: 18px;
    font-family: $second-family;
    font-weight: 700;
    font-size: $title-size;
    line-height: 104%;
    color: $dark-color;

    @include small-desktop {
      margin-bottom: 25px;
    }
    @include medium-tablet {
      margin-bottom: 17px;
      font-size: 22px;
    }
    @include small-mobile {
      font-size: 18px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 300px;
    grid-auto-rows: 258px;
    gap: 30px;

    @include small-desktop {
      grid-template-rows: 332px;
      grid-auto-rows: 186px;
    }
    @include medium-tablet {
      grid-template-rows: 228px;
      grid-auto-rows: 133px;
    }
    @include small-mobile {
      grid-template-rows: 186px;
      grid-auto-rows: 105px;
    }
  }

  &__item {
    grid-column: 3 span;

    &:first-child {
      grid-column: 6 span;
    }
  }

  &__image {
    height: 100%;
  }
}
