.header {
  padding: 24px 0 22px;
  background-color: $dark-color;
  @include small-desktop {
    padding: 26px 0 25px;
  }
  @include small-mobile {
    padding: 23px 0;
  }

  &__container {
    align-items: center;
  }

  &__bottom {
    grid-column: 4 / 7 span;
    @include flex-all-sb;

    @include small-desktop {
      grid-column: 3 / 5 span;
    }
    @include medium-tablet {
      grid-column: 6 span;
      position: absolute;
      top: 80px;
      left: 0;
      padding: 13px $container-offset-tablet;
      width: 100%;
      background-color: $bg-dark-grey-color;
    }
    @include small-mobile {
      top: 70px;
      padding: 13px $container-offset-mobile;
    }
  }

  &__btn {
    grid-column: 1 span;
    grid-column-end: 13;
    position: relative;
    z-index: 1;
    margin-left: auto;
    color: $light-color;
    cursor: pointer;
    transition: color .3s ease-in-out;

    @include small-desktop {
      grid-column-end: 9;
    }

    @include medium-tablet {
      grid-column-end: 7;
    }

    @include mobile {
      grid-column-end: 5;
    }

    &-icon {
      fill: currentColor;

      @include small-mobile {
        width: 20px;
        height: 20px;
      }
    }

    // &::before {
    //   content: '';
    //   position: absolute;
    //   z-index: -1;
    //   inset: -4px 5px 0 -5px;
    //   width: calc(100% + 10px);
    //   height: calc(100% + 4px);
    //   background-color: $focus-color;
    //   opacity: 0;
    //   visibility: hidden;
    //   transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    // }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $accent-color;
    }

    &:active {
      color: $active-color;
    }
  }
}

.header__btn.focus-visible  {
  color: $focus-color;
  // &::before {
  //   opacity: 1;
  //   visibility: visible;
  // }
}
