
.nav {
  width: 100%;

  @include mobile {
    @include flex;
    flex-direction: column;
    position: absolute;
    z-index: 99;
    top: 0;
    left: 0;
    padding: 48px 18px 36px;
    width: 60%;
    min-width: 320px;
    min-height: 541px;
    background-color: $dark-color;
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    transition: transform .3s ease-in-out, opacity .3s ease-in-out, visibility .3s ease-in-out;
  }
  @include small-mobile {
    width: 100%;
    max-width: none;
  }
  &__list {
    @include flex-all-sb;

    @include small-desktop {
    }

    @include medium-tablet {
      justify-content: space-between;
    }
    @include mobile {
      flex-direction: column;
      align-items: start;
      justify-content: start;
      margin-bottom: auto;
    }
  }

  &__item {
    @include small-desktop {
      margin-right: 33px;
    }
    @include medium-tablet {
      margin-right: 0;
    }
    &:not(:last-child) {
      margin-right: 25px;

      @include medium-tablet {
        margin-right: 15px;
      }
      @include mobile {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }
  &__link {
    position: relative;
    z-index: 1;
    font-family: $second-family;
    font-weight: 400;
    font-size: $main-size;
    color: $light-color;
    cursor: pointer;
    transition: color .3s ease-in-out;

    @include medium-tablet {
      letter-spacing: 0.5px;
    }

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: -5px;
      bottom: 0;
      right: 5px;
      width: calc(100% + 10px);
      background-color: $focus-color;
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $accent-color;
    }

    &:active {
      color: $active-color;
    }
  }
}

.nav__link.focus-visible  {
  &::before {
    opacity: 1;
    visibility: visible;
  }
}
.menu--active {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
}
.nav .btn-close {
  display: none;
  position: absolute;
  top: 14px;
  left: 21px;

  @include mobile {
    display: block;
  }
}
