.contacts {
  padding: 68px 0;
  background-color: $light-color;

  @include mobile {
    padding: 38px 0 50px;
  }

  &__title {
    margin-bottom: 16px;
  }

  &__content {
    position: relative;
    align-items: end;

    @include medium-tablet {
      row-gap: 35px;
    }
  }

  &__info {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    max-width: 410px;
    min-height: 460px;
    padding: 75px 60px 50px;
    background-color: rgba(32, 32, 32, 0.85);

    @include small-desktop {
      padding: 25px 30px 21px;
      max-width: 61.5%;
      min-height: 157px;
    }
    @include medium-tablet {
      bottom: 50%;
      max-width: 100%;
    }
    @include mobile {
      left: -($container-offset-mobile);
      bottom: 49.12%;
      right: -($container-offset-mobile);
      min-width: 100vw;
      padding: 25px 20px 14px 18px;
    }
  }

  &__map {
    grid-column: 8 span;
    min-height: 460px;
    -o-object-fit: cover;
       object-fit: cover;

    @include small-desktop {
      grid-column: 5 span;
    }
    @include medium-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 4 span;
      margin-left: -($container-offset-mobile);
      margin-right: -($container-offset-mobile);
      min-width: 100vw;
    }
    @include small-mobile {
      grid-column: 2 span;
    }
  }

  &__form {
    grid-column: 4 span;
    @include small-desktop {
      grid-column: 3 span;
    }
    @include medium-tablet {
      grid-column: 4 span;
    }
    @include mini-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 4 span;
    }
    @include small-mobile {
      grid-column: 2 span;
    }
  }
}

[class*="ground-pane"] {
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}

