.tel {
  display: none;
  color: $accent-color;

  @include mobile {
    @include flex-v-center;
  }
  &__icon {
    fill: currentColor;
    width: 20px;
    height: 20px;
  }
  &__text {
    color: currentColor;
  }
  @include hover {
    color: $light-color;
  }

  &:active {
    color: $active-color
  }
}
