.btn-close {
  border-radius: 100%;
  width: 18px;
  height: 18px;
  color: $light-color;
  background-color: #808080;
  transition: background-color .3s ease-in-out;

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 8.4px;
    height: 8.4px;
    fill: currentColor;
    transform: translate(-50%, -50%);
  }

  &:focus {
    outline: none;
  }

  @include hover {
    background-color: $accent-color;
  }

  &:active {
    background-color: $active-color;
  }
}
.btn-close.focus-visible {
  background-color: $focus-color;
}
