html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: $font-family, sans-serif;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  background-color: transparent;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.textarea-reset {
  resize: none;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 $container-offset;
  max-width: calc($content-width + ($container-offset * 2));

  @include small-tablet {
    padding: 0 $container-offset-tablet;
  }
  @include mobile {
    padding: 0 $container-offset-mobile;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 29px 30px;

  @include small-desktop {
    grid-template-columns: repeat(8, 1fr);
  }
  @include medium-tablet {
    grid-template-columns: repeat(6, 1fr);
  }
  @include mobile {
    grid-template-columns: repeat(4, 1fr);
  }
  @include small-mobile {
    grid-template-columns: repeat(2, 1fr);
  }
}

.section-title {
  margin: 0;
  font-family: $second-family;
  font-weight: 700;
  font-size: $title-size;
  @include small-mobile {
    font-size: 18px;
    line-height: 23px;
  }
}


