.post {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: start;
  height: 100%;

  @include small-desktop {
    height: auto;
    min-height: 100%;
  }

  @include medium-tablet {
    min-height: 300px;
  }
  @include mobile {
    justify-content: flex-end;
    min-height: 278px;
  }

  &--project {
    padding: 22px 30px 30px;

    @include small-desktop {
      padding: 30px;
    }

    @include mobile {
      min-height: 422px;
    }
  }

  &__category {
    position: absolute;
    top: 7px;
    right: 7px;
    padding: 6px 14px;
  }

  &__picture {
    width: 100%;
  }

  &__img {
    margin-bottom: 15px;
    width: 100%;

    &--project {
      position: absolute;
      inset: 0;
      z-index: -1;
      margin: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: $main-size;
    line-height: 22px;
      /* Обрезка текста */
    overflow-y: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    color: $dark-color;


    @include small-desktop {
      margin-bottom: 17px;
      line-height: 137%;
      -webkit-line-clamp: 2;
    }
    @include medium-tablet {
      margin-bottom: 10px;
      -webkit-line-clamp: 3;
    }


    &--project {
      margin-bottom: auto;
      max-width: 292px;
      color: $light-color;

      @include mobile {
        margin-bottom: 17px;
        font-size: 14px;
        line-height: 129%;
        -webkit-line-clamp: 4;
      }
    }
  }

  &__link {
    position: absolute;
    inset: 0;
    z-index: 2;
    outline: 2px solid transparent;
    outline-offset: 5px;
    cursor: pointer;

    &:focus {
      outline: none;
    }

    @include hover {
      outline-color: $accent-color;
    }

    &:active {
      outline-color: $active-color;
    }
  }

  &__date {
    margin-top: auto;
  }

  &__title {
    margin-bottom: 6px;
    max-width: 292px;
    line-height: 24px;
    color: #fff;

    @include mobile {
      margin-bottom: 9px;
      font-size: 24px;
    }
  }

  &__btn {
    background-color: transparent;

    @include hover {
      border-color: $accent-color;
      background-color: $accent-color;
    }

    &:active {
      border-color: $active-color;
      background-color: $active-color;
    }
  }
}
.post__btn.focus-visible  {
  border-color: $accent-color;
  background-color: $focus-color;
}
.post__link.focus-visible  {
  outline: 2px solid $focus-color;
  outline-offset: 5px;
}
