.footer {
  padding: 24px 0 16px;
  background-color: $dark-color;
  align-items: center;

  @include mini-tablet {
    position: relative;
    padding: 30px 0 14px;
    background-color: transparent;
  }

  &__container {
    align-items: center;

    @include mini-tablet {
      justify-items: center;
    }
  }

  &__left {
    grid-column: 3 span;

    @include medium-tablet {
      grid-column: 4 span;
    }
    @include mini-tablet {
      grid-column: 6 span;
      padding-top: 14px;
      text-align: center;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: -1;
        width: 100vw;
        min-height: calc(54.5px + 14px*2);
        background-color: $bg-dark-grey-color;
      }
    }
  }

  &__logo {
    margin-bottom: 6px;

    @include small-mobile {
      margin-bottom: 0;
    }
  }

  &__copyright {
    font-family: $second-family;
    font-weight: 400;
    font-size: 14px;
    color: #e9e9e9;

    @include small-mobile {
      font-size: 10px;
    }
  }

  &__quote {
    grid-column: 5 / 4 span;
    margin: 0;
    font-weight: 400;
    font-size: $main-size;
    line-height: 22px;
    white-space: pre-line;
    color: $silver-color;

    @include small-desktop {
      grid-column: 4 / 3 span;
      min-width: 361px;
    }

    @include medium-tablet {
      display: none;
    }
  }

  &__social {
    grid-column: 2 span;
    grid-column-end: 13;

    @include small-desktop {
      grid-column-end: 9;
    }
    @include mini-tablet {
      grid-column: 6 span;
      grid-column-end: auto;
      grid-row-start: 1;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100vw;
        min-height: calc(64px + 30px*2);
        background-color: $dark-color;
        ;
      }
    }
  }
}

.social {
  justify-self: end;
  max-width: 162px;

  @include mini-tablet {
    justify-self: auto;
  }
  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 400;
    font-size: $main-size;
    line-height: 22px;
    color: $silver-color;
  }
  &__list {
    justify-content: center;
    align-items: center;
    align-self: center;
    gap: 20px;
  }
  &__link {
    display: inline-block;
    width: 100%;
    height: 100%;
    color: $silver-color;
    transition: color .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: $accent-color;
    }

    &:active {
      color: $active-color;
    }
  }
  &__icon {
    width: 30px;
    height: 30px;
    fill: currentColor;
  }
}
.social__link.focus-visible  {
  color: $focus-color;
}
