.category {
  z-index: 5;
  padding: 7px 15px;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  color: $light-color;
  background-color: $dark-color;

  @include small-mobile {
    padding: 4px 7px;
    font-size: 12px;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    background-color: $accent-color;
  }

  &:active {
    background-color: $active-color;
  }
}
.category.focus-visible  {
  background-color: $focus-color;
}
