.info {
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

  @include small-desktop {
    width: 100%;
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }
  &__btn {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 20px;
    height: 20px;
    color: $light-color;
    background-color: #999;
    transition: background-color .3s ease-in-out;

    &-icon {
      width: 9.28px;
      height: 9.28px;
      fill: currentColor;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      background-color: $accent-color;
    }

    &:active {
      background-color: $active-color;
    }
  }

  &__company {
    margin: 0;
    margin-bottom: 5px;
    font-family: $second-family;
    font-weight: 400;
    font-size: $title-size;
    line-height: 22px;
    color: $light-color;

    @include small-mobile {
      margin-bottom: 10px;
      font-size: 18px;
      line-height: 122%;
    }
  }

  &__address {
    margin: 0;
    margin-bottom: 2px;
    font-family: $font-family;
    font-weight: 400;
    font-size: $main-size;
    line-height: 28px;
    color: $silver-color;

    @include small-desktop {
      max-width: 436px;
    }
    @include small-mobile {
      margin-bottom: 11px;
      font-size: 14px;
      line-height: 129%;
    }
  }

  &__tel {
    display: flex;
    align-items: center;
    font-family: $second-family;
    font-weight: 400;
    font-size: $main-size;
    color: $accent-color;
    transition: color .3s ease-in-out;

    @include small-mobile {
      font-weight: 500;
      font-size: 14px;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $light-color;
    }

    &:active {
      color: $active-color;
    }
  }

  &__tel-icon {
    width: 22px;
    height: 22px;
    fill: currentColor;
  }
}
.info__btn.focus-visible {
  background-color: $focus-color;
}

.info__tel.focus-visible {
  color: $focus-color;
}
