.form {
  .just-validate-error-field {
    border-color: #ff3030;
  }
  .just-validate-error-label {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 9px;
    color: #ff3030!important;
  }

  &__title {
    margin: 0;
    margin-bottom: 5px;
    font-family: $second-family;
    font-weight: 400;
    font-size: $title-size;
    line-height: 22px;
    color: $dark-color;

    @include small-mobile {
      font-size: $mobile-title-size;
      line-height: 122%;
    }
  }

  &__comment {
    margin: 0;
    margin-bottom: 14px;
    font-weight: 400;
    font-size: $main-size;
    line-height: 22px;
    color: #999;

    @include small-mobile {
      font-size: $mobile-text-size;
      line-height: 129%;
    }
  }
  &__label {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
  }
  &__input {
    margin-bottom: 20px;
    padding: 15px 23px 16px;
    border: 1px solid #e9e9e9;
    width: 100%;
    background-color: #e9e9e9;

    @include small-mobile {
      padding: 10px 23px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: $main-size;
      color: #999;

      @include small-mobile {
        font-size: $mobile-text-size;
      }
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $accent-color;
    }

    &:active {
      color: $dark-color;
    }
  }
  &__textarea {
    margin-bottom: 15px;
    padding: 15px 23px 16px;
    border: 1px solid #e9e9e9;
    width: 100%;
    min-height: 150px;
    background-color: #e9e9e9;

    @include small-mobile {
      margin-bottom: 20px;
      padding: 10px 23px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: $main-size;
      color: #999;

      @include small-mobile {
        font-size: $mobile-text-size;
      }
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $accent-color;
    }

    &:active {
      color: $dark-color;
    }
  }
}
.form__input.focus-visible {
  border-color: $accent-color;
}
.form__textarea.focus-visible {
  border-color: $accent-color;
}

.form__input:-webkit-autofill,
.form__inputt:-webkit-autofill:hover,
.form__input:-webkit-autofill:focus {
  -webkit-text-fill-color: $dark-color;
  -webkit-box-shadow: 0 0 0px 40rem #e9e9e9 inset;
}
