.logo {
  grid-column: 2 span;
  display: block;
  color: $light-color;

  @include small-mobile {
    grid-column: 1 span;
    max-height: 18px;
  }

  &__icon {
    fill: currentColor;

    @include small-desktop {
      width: 166px;
      height: 26px;
    }
    @include small-mobile {
      width: 112px;
      height: 18px;
    }
  }
}
