.search {
  position: absolute;
  z-index: 99;
  right: calc((100% - $content-width)/2);
  @include flex-v-center;
  padding: 0px 2px 2px 2px;
  background-color: $dark-color;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

  @include medium-desktop {
    right: $container-offset;
  }
  @include medium-tablet {
    padding: 0px 2px 2px 2px;
    right: $container-offset-tablet;
  }

  @include mobile {
    right: $container-offset-mobile;
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }

  &__field {
    margin-right: 9px;
    padding: 5px 0 4px;
    border-bottom: 1px solid #e9e9e9;
    width: 308px;
    color: $light-color;
    background-color: $dark-color;
    &::placeholder {
      font-family: $second-family;
      font-weight: 400;
      $main-size: 16px;
      color: #e9e9e9;
    }
    @include mobile {
      width: calc(100vw - ($container-offset-mobile*2) - 23px - 9px - 4px);
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border-bottom-color: $accent-color;
    }

    &:active {
      border-bottom-color: $active-color;
    }
  }

  &__btn-close {
    position: relative;
    width: 23px;
    height: 23px;

    @include medium-tablet {
      margin: 0 4px 2px 0px;
    }
    @include small-tablet {
      margin: 0;
    }
  }
}
.search__field.focus-visible {
  border-bottom-color: $accent-color;
}
.search__field:-webkit-autofill,
.search__field:-webkit-autofill:hover,
.search__field:-webkit-autofill:focus {
  -webkit-text-fill-color: $light-color;
  -webkit-box-shadow: 0 0 0px 40rem $dark-color inset;
}
