.projects {
  padding: 68px 0 0;

  @include mobile {
    padding: 50px 0 0;
  }

  &__title {
    margin-bottom: 15px;
    color: $dark-color;
  }
  &__list {
    @include small-desktop {
      grid-auto-flow: dense;
      row-gap: 24px;
    }
    @include medium-tablet {
      row-gap: 28px;
    }
    @include mobile {
      row-gap: 24px;
    }
  }
  &__item {
    &:nth-child(-n+3) {
      grid-column: 2 span;
    }
    &:nth-child(4n) {
      grid-column: 6 span;
    }
    &:nth-child(5n) {
      grid-column: 6 span;
    }
    &:nth-child(n+6) {
      grid-column: 3 span;
    }

    @include small-desktop {
      &:nth-child(-n+2) {
        grid-column: 2 span;
      }
      &:nth-child(3n) {
        grid-column: 4 span;
      }
      &:nth-child(4n) {
        grid-column: 5 span;
      }
      &:nth-child(n+6) {
        grid-column: 3 span;

        .post__descr {
          -webkit-line-clamp: 3;
        }
      }
      &:nth-child(5n) {
        grid-column: 5 span;
        grid-column-end: 9;
      }
    }

    @include medium-tablet {
      &:nth-child(-n+3) {
        grid-column: 2 span;
      }
      &:nth-child(4n) {
        grid-column: 6 span;
      }
      &:nth-child(5n) {
        grid-column: 6 span;
        grid-column-end: auto;
        grid-row-end: 5;
      }
      &:nth-child(n+6) {
        grid-column: 3 span;
      }
    }
    @include mobile {
      &:nth-child(n) {
        grid-column: 4 span;
      }
    }
    @include small-mobile {
      &:nth-child(n) {
        grid-column: 2 span;
      }
      &:nth-child(3) {
        .post__descr {
          -webkit-line-clamp: 2;
        }
      }
      &:nth-child(5n) {
        grid-row-end: 8;
      }
    }
  }
}
