  // base
  $font-family: "Roboto", sans-serif;
  $second-family: "TTFirsNeue", sans-serif;
  $content-width: 1290px;
  $container-offset: 30px;
  $container-offset-tablet: 27px;
  $container-offset-mobile: 20px;
  $container-width: calc($content-width + ($container-offset * 2));
  // sizes
  $main-size: 16px;
  $title-size: 24px;
  $mobile-title-size: 18px;
  $mobile-text-size: 14px;
  // colors
  $light-color: #fff;
  $dark-color: #202020;
  $accent-color: #ff6e30;
  $focus-color: #bc572b;
  $active-color: #ce4307;
  $bg-light-color: #e5e5e5;
  $silver-color: #cacaca;
  $bg-dark-grey-color: #4a4a4a;
